'use client'
import React from 'react'
import CommunityUserAvatar from './community-user-avatar'
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Button } from './button'
import { useEffect, useState } from 'react'
import axios from 'axios'
import Link from 'next/link'
import {
    Card,
    CardAction,
    CardContent,
    CardDescription,
    CardFooter,
    CardHeader,
    CardTitle,
} from "@/components/ui/card"

const communities = ({id}:{id:string}) => {
interface CommunityType {
  id: number
  communityOwnerName: string
  totalCarbonEmission: number,
  communityName: string
  communityAvatar: string
  CommunityAvatarPublicId?: string
  totalParticipants: number
  communityOwnerId: number
  createdAt?: string
  updatedAt?: string
}

const [communities, setCommunities] = useState<CommunityType[]>([])

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res = await axios.get(
          `http://localhost:3000/api/community/current-community?communityId=${Number(id)}`
        )
        setCommunities(res.data?.community || [])

      } catch (error) {
        console.error('Error fetching communities:', error)
      }
    }

    fetchData()
  }, [communities])



    return (
        <div className='flex flex-col items-center h-auto  w-full mr-3  '>
           
            {
                communities.map((i:CommunityType)=>{
                    return <div className='w-full flex flex-col items-center'>
                <Avatar className='size-40 mt-1'>
                <AvatarImage src={i.communityAvatar} />
                <AvatarFallback>CN</AvatarFallback>
            </Avatar>

            <div className=' py-3 px-20 w-full items-center flex flex-col border-2 rounded-2xl my-1 border-green-600 '>
                <CardTitle className='text-2xl text-green-700'>{i.communityName} </CardTitle>
                <CardHeader className='w-full  flex flex-col ' >
                    <CardDescription>Carbon emmisson : {i.totalCarbonEmission}</CardDescription>
                    <CardDescription>Total member : {i.totalParticipants} </CardDescription>
                    <CardDescription>Created by : {i.communityOwnerName}</CardDescription>
                </CardHeader>

                <CardFooter className='w-full  justify-between py-2 text-green-700'>
                    <Link href={`/explore-more/${id}`}><CardTitle> <Button variant={'outline'}>expore more </Button></CardTitle></Link>
                    <CardAction><CardTitle className='text-green-700'>
                        <Button variant={'outline'}>JOIN </Button></CardTitle></CardAction>
                    <Link href={`/community-chats/${id}`}> <Button variant={'outline'}>Chats </Button></Link>
                </CardFooter>
            </div>
          
            </div>
                })
            }

            <div className=' text-xl py-1.5 font-semibold text-green-700 px-20 w-full items-center flex flex-col border-2 rounded-2xl my-1 border-green-600'>Total Members</div>
            <div className=' w-full gap-1 flex flex-col '>
            <CommunityUserAvatar/>
            <CommunityUserAvatar/>
            <CommunityUserAvatar/>
            <CommunityUserAvatar/>
            <CommunityUserAvatar/>
            </div>
        
        </div>
    )
}

export default communities
